.card {
  background: #fff;
  width: 200px;
  height: 406px;
  padding: 0;
  font-family: Roboto, sans-serif;
  position: relative;
  box-shadow: 0 2px 6px #0000001a;
}

.card__image {
  width: 160px;
  height: 134px;
  position: absolute;
  top: 32px;
  left: 20px;
}

.card__image img {
  width: 100%;
  height: 100%;
  display: block;
}

.card__name {
  color: #060b35;
  height: 36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  position: absolute;
  top: 206px;
  left: 16px;
  right: 16px;
}

.card__code {
  color: #616070;
  width: 98px;
  height: 14px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  position: absolute;
  top: 247px;
  left: 17px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  display: flex;
  position: absolute;
  top: 277px;
  left: 17px;
}

.card__review-count {
  color: #616070;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price-box {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  display: flex;
  position: absolute;
  top: 316px;
  left: 16px;
}

.card__price-box-text {
  font-size: 12px;
}

.card__price-box-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: absolute;
  top: 350px;
  left: 16px;
}

.card__buy:hover {
  background-color: #0095b8;
}

.stars {
  gap: 2px;
  display: flex;
}

.stars__star {
  background-image: url("star.75a8b2fa.svg");
  background-size: cover;
  width: 16px;
  height: 16px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.896e078e.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}
